___CSS_LOADER_EXPORT___.push([module.id, "\n.slot-frame[data-v-2aa0f733] {\n        border: 1px solid black;\n        border-radius: 4px;\n        width: 100%;\n        margin-bottom: 4px;\n        display: flex;\n        flex-direction: row;\n}\n.slot-input[data-v-2aa0f733],.slot-output[data-v-2aa0f733] {\n        display: flex;\n        flex-direction: row;\n        width: 20px;\n        padding: 2px;\n}\n.slot-point[data-v-2aa0f733] {\n        display: flex;\n        flex-direction: column;\n}\n.slot-point .mark[data-v-2aa0f733] {\n        width: 16px;\n        height: 16px;\n        border: 1px solid black;\n}\n.slot-point .mark.slot-solid[data-v-2aa0f733] {\n        border-radius: 0px;\n}\n.slot-point .mark.slot-fluid[data-v-2aa0f733] {\n        border-radius: 16px;\n}\n.slot-content[data-v-2aa0f733] {\n        flex: 1;\n        padding: 2px;\n}\n.slot-content material-view[data-v-2aa0f733] {\n        width: 100%;\n}\n.abnormal[data-v-2aa0f733] {\n        color: chocolate;\n}\n.v-separator[data-v-2aa0f733] {\n        width: 1px;\n        background: black;\n}\n.mouseover-possible[data-v-2aa0f733] {\n        border-color: green;\n}\n.mouseover-impossible[data-v-2aa0f733] {\n        border-color: red;\n}\n.socketAccent[data-v-2aa0f733] {\n        background: black;\n}\n", "",{"version":3,"sources":["webpack://./src/node_editor/NodeSocket.vue"],"names":[],"mappings":";AA0KI;QACI,uBAAuB;QACvB,kBAAkB;QAClB,WAAW;QACX,kBAAkB;QAClB,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,aAAa;QACb,mBAAmB;QACnB,WAAW;QACX,YAAY;AAChB;AACA;QACI,aAAa;QACb,sBAAsB;AAC1B;AACA;QACI,WAAW;QACX,YAAY;QACZ,uBAAuB;AAC3B;AACA;QACI,kBAAkB;AACtB;AACA;QACI,mBAAmB;AACvB;AACA;QACI,OAAO;QACP,YAAY;AAChB;AACA;QACI,WAAW;AACf;AACA;QACI,gBAAgB;AACpB;AACA;QACI,UAAU;QACV,iBAAiB;AACrB;AACA;QACI,mBAAmB;AACvB;AACA;QACI,iBAAiB;AACrB;AACA;QACI,iBAAiB;AACrB","sourcesContent":["<template>\n    <div class=\"slot-frame\" :style=\"frameClass\"\n            @mousedown=\"onMouseAction\"\n            @mouseup=\"onMouseAction\"\n            @mouseenter=\"onMouseAction\"\n            @mouseleave=\"onMouseAction\">\n        <div class=\"slot-input\" v-if=\"isInput\">\n            <div class=\"slot-point\">\n                <div ref=\"inputSocket\" v-if=\"isSolid\"\n                    class=\"mark slot-solid\" :class=\"{socketAccent: isSocketAccent}\"></div>\n                <div ref=\"inputSocket\" v-if=\"isFluid\"\n                    class=\"mark slot-fluid\" :class=\"{socketAccent: isSocketAccent}\"></div>\n            </div>\n        </div>\n        <div class=\"v-separator\" v-if=\"isInput\"></div>\n        <div class=\"slot-content\">\n            <material-view ref=\"material\" :material=\"material\" :num=\"num\" :machineInfo=\"machineInfo\"></material-view>\n        </div>\n        <div class=\"v-separator\" v-if=\"isOutput\"></div>\n        <div class=\"slot-input\" v-if=\"isOutput\" ref=\"outputSocket\">\n            <div class=\"slot-point\">\n                <div ref=\"outputSocket\" v-if=\"isSolid\"\n                    class=\"mark slot-solid\" :class=\"{socketAccent: isSocketAccent}\"></div>\n                <div ref=\"outputSocket\" v-if=\"isFluid\"\n                    class=\"mark slot-fluid\" :class=\"{socketAccent: isSocketAccent}\"></div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport { IODirection, MouseButton, MaterialState, EditorControlState } from 'const'\nimport { Config } from 'models/Config'\nimport { mapGetters, mapMutations } from 'vuex'\nimport MaterialView from 'node_editor/MaterialView'\n\nexport default {\n    components: {\n        MaterialView\n    },\n    props: {\n        direction: { // ソケットの向き\n            type: String,\n            default: IODirection.Input,\n        },\n        material: { // 素材ID\n            type: String,\n            default: '',\n        },\n        num: { // 素材数\n            type: Number,\n            default: 1,\n        },\n        machineInfo: { // 設備情報\n            type: Object,\n            default: {},\n        },\n    },\n    computed: {\n        ...mapGetters('EditorStatus', {\n            isIdling: 'isIdling', // 無操作中か\n            isConnecting: 'isConnecting', // 運搬ライン接続中か\n            originPoint: 'originPoint', // ページ上のエディタの原点の位置\n        }),\n        // 入力側を示す定義名\n        isInput() {\n            return this.direction == IODirection.Input;\n        },\n        // 出力側を示す定義名\n        isOutput() {\n            return this.direction == IODirection.Output;\n        },\n        // 固形を表す定義\n        isSolid() {\n            return this.state == MaterialState.Solid;\n        },\n        // 流形を表す定義\n        isFluid() {\n            return this.state == MaterialState.Fluid;\n        },\n        // 物質の状態\n        state() {\n            return Config.getMaterialData(this.material).State;\n        },\n    },\n    methods: {\n        ...mapMutations('EditorStatus', {\n            changeControlState: 'changeState', // 操作ステータス変更\n        }),\n        // 外向け関数\n        getSocketPoint: function() {\n            let rect = new DOMRect();\n            /*\n            if (this.isInput) {\n                rect = this.$refs.inputSocket.getBoundingClientRect();\n            }\n            else if (this.isOutput) {\n                rect = this.$refs.outputSocket.getBoundingClientRect();\n            }\n            */\n            return {\n                x: rect.x + rect.width * 0.5 - this.originPoint.x,\n                y: rect.y + rect.height * 0.5 - this.originPoint.y,\n            };\n        },\n        // 連結可能か(同じ素材か)\n        canConnectLine: function(material) {\n            if (!this.material) return true; // 素材指定無しの場合はなんでもOK\n            return this.material === material;\n        },\n        // 運搬ラインとソケット連結\n        connectLine: function(line) {\n            if (line in this.joinningLines) return;\n\n            this.joinningLines.push(line);\n        },\n        onMoved: function() {\n            // ノードが移動したら繋がっているラインにも伝達\n            this.joinningLines.forEach(v => v.onMoved());\n        },\n        onMouseAction: function(e) {\n            /*\n            // マウス左ボタンが押されたら接続開始を通知\n            if (e.type == 'mousedown' && e.button == MouseButton.Left) {\n                if (this.isIdling) {\n                    this.changeControlState(EditorControlState.Connect);\n                    this.$emit('startconnection', {\n                        event: e,\n                        socket: this, // 同じソケットへの接続を回避するため設定\n                        socketPos: this.getSocketPoint(),\n                    });\n                }\n            }\n            // マウス左ボタンが離されたら接続終了を通知\n            else if (e.type == 'mouseup' && e.button == MouseButton.Left) {\n                if (this.isConnecting) {\n                    this.$emit('endconnection', {\n                        event: e,\n                        socket: this, // 同じソケットへの接続を回避するため設定\n                        socketPos: this.getSocketPoint(),\n                    });\n                }\n            }\n            // マウスホバーでソケット強調表示\n            else*/ if (e.type == 'mouseenter') {\n                this.isSocketAccent = true;\n                e.stopPropagation();\n                return false;\n            }\n            else if (e.type == 'mouseleave') {\n                this.isSocketAccent = false;\n                e.stopPropagation();\n                return false;\n            };\n        }\n    },\n    data: function() {\n        return {\n            joinningLines: [], // 接続されている運搬ラインリスト\n            frameClass: { // フレーム部に適用するマウス操作によるクラス群\n                'mouseover-possible': false,\n                'mouseover-impossible': false,\n            },\n            isSocketAccent: false, // ソケット部分の強調フラグ\n        };\n    }\n}\n</script>\n\n<style scoped>\n    .slot-frame {\n        border: 1px solid black;\n        border-radius: 4px;\n        width: 100%;\n        margin-bottom: 4px;\n        display: flex;\n        flex-direction: row;\n    }\n    .slot-input,.slot-output {\n        display: flex;\n        flex-direction: row;\n        width: 20px;\n        padding: 2px;\n    }\n    .slot-point {\n        display: flex;\n        flex-direction: column;\n    }\n    .slot-point .mark {\n        width: 16px;\n        height: 16px;\n        border: 1px solid black;\n    }\n    .slot-point .mark.slot-solid {\n        border-radius: 0px;\n    }\n    .slot-point .mark.slot-fluid {\n        border-radius: 16px;\n    }\n    .slot-content {\n        flex: 1;\n        padding: 2px;\n    }\n    .slot-content material-view {\n        width: 100%;\n    }\n    .abnormal {\n        color: chocolate;\n    }\n    .v-separator {\n        width: 1px;\n        background: black;\n    }\n    .mouseover-possible {\n        border-color: green;\n    }\n    .mouseover-impossible {\n        border-color: red;\n    }\n    .socketAccent {\n        background: black;\n    }\n</style>"],"sourceRoot":""}]);
